html {
  font-family: "Source Serif Pro";
  font-size: 20px;
  line-height: 1.6;
}

body {
  max-width: 1050px;
  margin: auto;
}

.App {
  display: grid;
  grid-template-columns: 800px minmax(auto, 250px);
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.tick line {
  opacity: .3;
}

.axis.label {
  font-family: "Source Serif Pro";
  font-size: 1rem;
}
